import { T_ConversionRates } from "shared/components/UnitConverter/types";

export function mapForexRateResponse(response: T_ForexResponse) {
  return {
    baseCode: response.base_code,
    conversionRates: response.conversion_rates,
    timeLastUpdate: response.time_last_update_unix,
    timeNextUpdate: response.time_next_update_unix
  };
}

/**
 * Types
 */
export interface T_ForexResponse {
  conversion_rates: T_ConversionRates;
  base_code: string;
  time_last_update_unix: number;
  time_last_update_utc: string;
  time_next_update_unix: number;
  time_next_update_utc: string;
}

export type T_ForexData = ReturnType<typeof mapForexRateResponse>;
