import React, { useEffect, useState } from "react";

import UnitConverter from "shared/components/UnitConverter";
import forexConstants from "./constants";
import { fetchForexData } from "./api";
import CountryFlag from "shared/components/CountryFlag";

export default function Forex() {
  const [conversionRates, setState] =
    useState<Awaited<ReturnType<typeof fetchForexData>>>();

  useEffect(() => {
    const fetchData = async () => {
      let result = await fetchForexData();

      if (result) {
        setState(result);
      }
    };

    fetchData();
  }, []);

  // TODO:loading indicator / animate-pulse empty content placholders
  if (!conversionRates) {
    return null;
  }

  return (
    <UnitConverter
      allUnitsMap={forexConstants.unitsMap}
      conversionRates={conversionRates}
      defaults={forexConstants.defaults}
      UnitIconComponent={CountryFlag}
    />
  );
}
