export const enum T_HTTPMethod {
  GET = "GET",
  POST = "POST",
  PUT = "PUT",
  PATCH = "PATCH",
  DELETE = "DELETE"
}

interface T_RequestOptions {
  method: T_HTTPMethod;
  endpoint: string;
  params?: { [key: string]: any };
  headers?: HeadersInit;
  body?: any;
  rawBody?: BodyInit | null;
}

export const getQueryParams = (params: { [key: string]: string }): string =>
  Object.keys(params)
    .filter(
      (k) =>
        typeof params[k] !== "undefined" &&
        params[k] !== null &&
        params[k] !== ""
    ) // removes if empty, but accepts "0"
    .map((k) => `${encodeURIComponent(k)}=${encodeURIComponent(params[k])}`)
    .join("&");

export default async function request<T>({
  method,
  endpoint,
  params,
  headers,
  body,
  rawBody
}: T_RequestOptions): Promise<{
  result?: T;
  error?: unknown;
}> {
  let url = endpoint;
  if (params) {
    url += (url.indexOf("?") === -1 ? "?" : "&") + getQueryParams(params);
  }

  const fetchOptions: RequestInit = {
    headers,
    method,
    credentials: "same-origin",
    mode: "cors"
  };
  if (method !== T_HTTPMethod.GET && (rawBody || body)) {
    fetchOptions.body = rawBody || JSON.stringify(body);
  }

  try {
    const response = await fetch(url, fetchOptions);
    const contentType = response.headers.get("Content-Type") || "";
    let result: T;

    if (contentType.includes("application/json")) {
      result = await response.json();
    } else {
      result = response as unknown as T;
    }

    if (response.status >= 400) {
      throw result || (await response.text());
    }

    return { result };
  } catch (error) {
    return { error: error instanceof Error ? error.message : error };
  }
}
